  line: function line(start, end, cfg) {
    var config = {
      start: start,
      end: end,
      cfg: Util.mix({}, cfg)
    };

    Util.mix(config, this._getDefault());
    var guide = new Guide.Line(config);
    this.addGuide(guide);
    return this;
  },

